import back from '@/components/back'
import noData from '@/components/noData'

export default {
    components:{
        back,
        noData
    },
    data(){
        return {
            search: '',
            page: 1,
            haveMore: true,
            list: [],
            orderInfo: this.$store.state.pInfo,
            user: this.$store.state.userInfo,
        }
    },
    created(){
        this.pull(false)
    },
    mounted(){
        this.getMore()
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods:{
        pull(h){
            if(this.haveMore){
                this.$post('PlatFormAPI/KnowledgeBase/GetDrQuestionnaireForPage',this.$pick({
                    QuestionnaireName: this.search,
                    QuestionnaireKey: '',
                    OrgIds: this.user.Data.User.OrgId,
                    PageIndex: this.page,
                    PageSize: 15,
                })).then(o => {
                    if (!o.ReturnData || o.ReturnData.length < 15){
                        this.haveMore = false
                    }
                    else {
                        this.haveMore = true
                    }
                    
                    if (h){
                        o.ReturnData.forEach(v => {
                            this.list.push(v)
                        })
                    }
                    else {
                        this.list = o.ReturnData
                    }
                })
            }
        },
        getMore(){
            const el = this.$refs.el
            let timer = null
            window.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
                    const top = document.documentElement.scrollTop
                                || window.pageYOffset
                                || document.body.scrollTop,
                            h = el.scrollHeight,
                            wh = window.innerHeight

                    if (h - top - wh < 100 && this.haveMore){
                        this.page += 1
                        this.pull(true)
                    }
                }, 100)
            }
        },
        send(i){
            this.$post('PlatFormAPI/KnowledgeBase/IssueQuestionnaire',{
                PatientId: this.orderInfo.PatientInfo.PatientId,
                DrId: this.user.Data.User.DoctorId,
                DrName: this.user.Data.User.DoctorName,
                QuestionnaireId: i.QuestionnaireId,
                OrgIds: this.user.Data.User.OrgId
            }).then(o => {
                this.$toast('发送成功')
                const data = '123'
                this.$bridge.callhandler('Back', data)
            })
        }
    }
}



